.box{
    font-family: 'Iceland', cursive;
    text-align: center;
    color: #FCF9F4;
    padding: 20px 40px;
    background-color: #1E1E1E;
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

h3{
    font-size: 30px;
    font-weight: 600;
}

.close{
    font-family: 'Iceland', cursive;
    background-color: #1E1E1E;
    color: white;
    border: none;
    font-weight: bold;
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px 10px;
    cursor: pointer;
}

.save{
    font-family: 'Iceland', cursive;
    background-color: white;
    color: #1E1E1E;
    border: 1px solid white;
    border-radius: 10px;
    font-weight: bold;
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 10px 10px;
    padding: 4px 15px;
    cursor: pointer;
}

.input-section{
    display: flex;
    flex-direction: row;
    margin-top: 30px;
    margin-bottom: 50px;
    text-transform: uppercase;
    font-family: 'Iceland', cursive;
}